<template>
    <div class="create">
        <action-bar title="创建商品"></action-bar>
        <div class="add">
            <div class="title">添加商品<span class="goods-census">共{{ goodsList.length }}件</span></div>
            <div class="release" @click="bindRelease">发布</div>
        </div>
        <div class="add-goods-box">
            <div class="no-font-size" @click="bindAddGoods">
                <img class="add-goods-img" src="@/assets/home_add_commoidy_icon.png" />
            </div>
            <div class="hor-goods-box">
                <div class="hor-scroll">
                    <van-image v-for="item in goodsList" :key="item.id" class="hor-goods-img"
                        :src="baseUrl + item.imageUrl" />
                </div>
            </div>
        </div>
        <!-- content -->
        <div class="content">
          <scroll-view class="scroll" :data="goodsList">
                  <div class="scroll-box">
                    <goods-item class="goods-box"   v-for="(item, index) in goodsList" :key="item.id">
                        <van-image radius=".25rem" slot="imgSlot" :src="baseUrl + item.imageUrl" class="goods-img" />
                        <div slot="nameSlot">
                            <img class="delete-img" src="@/assets/icon_chongzhi.png" @click="bindDeleteGoods(index)" />
                            <div>{{ item.skuName }}</div>
                        </div>
                        <div slot="attributeSlot">
                            <div class="attribute">
                                <div class="attribute-content">分类：{{ item.categoryName }}</div>
                                <div class="attribute-content">颜色：{{ item.color }}</div>
                            </div>
                            <div class="attribute">
                                <div class="attribute-content">等级：{{ item.level }}</div>
                                <div class="attribute-content">规格：{{ item.unit }}</div>
                            </div>
                        </div>
                        <div slot="menuSlot" class="goods-bottom">
                            <div class="price-stock">
                                <span>价格(元)</span>
                                <input type="number" v-model="item.priceStay" maxlength="10" placeholder="0.00元"
                                    class="input-bg" />
                            </div>
                            <div class="price-stock">
                                <span>库存(扎)</span>
                                <input type="number" maxlength="10" placeholder="0扎" v-model="item.stockStay"
                                    class="input-bg" />
                            </div>
                        </div>
                    </goods-item>
                  </div>
                </scroll-view>
        </div>
    </div>
</template>
<script>
import { libraryInfo, libraryEdit, addProduct } from '@/http/api'
import GoodsItem from '@/components/util/GoodsItem.vue'
import { List } from 'vant'
export default {
  name: 'releaseGoods',
  components: {
    [GoodsItem.name]: GoodsItem,
    [List.name]: List
  },
  data () {
    return {
      goodsList: [] // 已添加的商品
    }
  },
  computed: {
    baseUrl () {
      return this.$store.state.baseUrl
    }
  },
  methods: {
    // 发布
    bindRelease () {
      const goodsList = this.goodsList
      if (goodsList.length === 0) {
        this.$toast('请添加商品')
        return
      }
      const obj = []
      for (const goods of goodsList) {
        if (!goods.priceStay) {
          this.$toast('<' + goods.skuName + '>价格不能为空')
          return
        }
        if (!goods.stockStay) {
          this.$toast('<' + goods.skuName + '>库存不能为空')
          return
        }
        obj.push({
          price: goods.priceStay,
          stock: goods.stockStay,
          skuId: goods.skuId
        })
      }
      const data = {
        data: JSON.stringify(obj)
      }
      addProduct(data).then(res => {
        this.$router.go(-1)
        this.$toast('创建成功')
      }).catch(res => {
        this.$toast(res.msg)
      })
    },
    bindAddGoods () {
      this.$router.push('/goodsCommodity')
    },
    bindDeleteGoods (index) {
      const goodsList = this.goodsList
      libraryEdit({
        skuId: goodsList[index].skuId
      }).then(res => {
        goodsList.splice(index, 1)
        this.goodsList = goodsList
      })
    },
    // 获取数据
    getGoodsList () {
      libraryInfo().then(res => {
        this.goodsList = res.data
      }).catch(res => {
        // this.$toast(res.msg)
      })
    }
  },
  mounted () {

  },
  activated () {
    this.getGoodsList()
  }
}
</script>
<style lang="scss" scoped>
.create {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
}

.add {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin-left: 1rem;
}

.title {
    font-size: 1rem;
}

.release {
    margin: .75rem;
    border-radius: .375rem;
    background-color: #42D18A;
    padding: .625rem 1.75rem;
    color: white;
    font-size: .875rem;
}

.goods-census {
    margin-left: .5rem;
    color: #999;
    font-size: .875rem;
}

.add-goods-box {
    display: flex;
    padding: .5rem 1rem;
}

.no-font-size {
    font-size: 0;
    margin-right: .5rem;
}

.add-goods-img {
    width: 2.625rem;
    height: 2.625rem;
    padding: .25rem;
    border: #333333 dashed .0625rem;
    border-radius: 2px;
    box-sizing: border-box;
}

.hor-goods-box {
    flex-grow: 1;
    position: relative;
}

.hor-scroll {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    white-space: nowrap;
    overflow-x: auto;
    overflow-y: hidden;
}

.hor-scroll::-webkit-scrollbar {
    display: none;
}

.hor-goods-img {
    width: 2.75rem;
    height: 2.75rem;
    background-color: #F2F2F2;
    border-radius: .25rem;
    font-size: 0;
    margin: 0 .25rem;
}

.content {
    flex-grow: 1;
    position: relative;
    background-color: #f2f2f2;

}

.scroll {
    background-color: #f2f2f2;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}

.scroll-box{
  padding: .5rem;
}

.goods-box {
    background-color: white;
    border-radius: .25rem;
    margin-bottom: .5rem;
}

.goods-img {
    width: 20vw;
    height: 20vw;
    background-color: #F2F2F2;
    margin-right: .5rem;
}

.delete-img {
    width: 1.125rem;
    height: 1.125rem;
    float: right;
    margin-left: .5rem;
}

.attribute {
    display: flex;
    flex-direction: row;
    font-size: .75rem;
}

.attribute-content {
    width: 50%;
}

.goods-bottom {
    display: flex;
    flex-direction: row;
    padding-left: .75rem;
    padding-right: .75rem;
    padding-top: .75rem;
    align-items: center;
    padding-bottom: .75rem;
    font-size: .875rem;
}

.price-stock {
    display: flex;
    flex-direction: row;
    width: 50%;
    white-space: nowrap;
    align-items: center;
}

.input-bg {
    background-color: #f5f7f9;
    border-radius: .25rem;
    border: none;
    box-sizing: border-box;
    padding-left: .75rem;
    padding-top: .5rem;
    padding-bottom: .5rem;
    width: 100%;
}

input::placeholder {
    color: #888F9F;
}
</style>
